import styled, { css } from "styled-components";

const breakpointsCss = ({ theme }) =>
  Object.values(theme.devices).map(
    ({ size, query }) => css`
      @media ${query} {
        max-width: calc(${size}px - calc(var(--grid-gutter) * 2));
      }
    `
  );

export default styled.div`
  width: 100%;
  margin: 0 auto;
  max-width: calc(100% - calc(var(--grid-gutter) * 2));

  ${breakpointsCss};
`;
