import React from "react";
import { withRouter } from "react-router";
import styled from "styled-components";
import { Helmet } from "react-helmet";

import AppBar from "./AppBar";
import AppFooter from "./AppFooter";
import Router from "./Router";

const AppWrapper = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
`;

const AppContent = styled.main`
  flex: 1;
`;

function App({ location, match }) {
  const origin =
    process.env.REACT_APP_LOCATION_ORIGIN || window.location.origin;
  const canonical =
    origin + process.env.PUBLIC_URL + location.pathname + location.search;

  return (
    <AppWrapper>
      <Helmet>
        <title>MTC</title>
        <link rel="canonical" href={canonical} />
        <meta name="description" content="Medecine traditionnelle chinoise" />
      </Helmet>

      <AppBar />

      <AppContent>
        <Router />
      </AppContent>

      <AppFooter />
    </AppWrapper>
  );
}

export default withRouter(App);
