import React from "react";
import { withRouter } from "react-router";
import { NavLink } from "react-router-dom";
import styled, { createGlobalStyle } from "styled-components";
import classnames from "classnames";

import PageContent from "./PageContent";
import Drawer from "./Drawer";

const AppBarGlobalStyles = createGlobalStyle`
  :root {
    --app-bar-height: 56px;

    @media ${({ theme }) => theme.devices.small.query} {
      --app-bar-height: 64px;
    }
  }
`;

const AppBarPlaceholder = styled.div`
  width: 100%;
  height: var(--app-bar-height);
`;

const AppBarWrapper = styled.header`
  position: fixed;
  top: 0;
  width: 100%;
  height: var(--app-bar-height);
  z-index: 100;
  display: flex;
  background-color: #444444;
  color: #fff;
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12),
    0 2px 4px -1px rgba(0, 0, 0, 0.2);
`;

const AppBarContent = styled(PageContent)`
  flex: 1;
  display: flex;
  justify-content: flex-end;
`;

const NavList = ({ className, ...props }) => (
  <nav className={className}>
    <ul className="nav-list" {...props} />
  </nav>
);

const NavListItem = ({ className, ...props }) => (
  <li className={className}>
    <NavLink className="nav-link" {...props} />
  </li>
);

const BarNavList = styled(NavList)`
  flex: 1;
  display: none;

  .nav-list {
    flex: 1;
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-around;
  }

  @media ${({ theme }) => theme.devices.small.query} {
    display: flex;
  }
`;

const BarNavListItem = styled(NavListItem)`
  flex: 1;
  display: flex;
  margin: ${({ theme }) => theme.gridGutter / 2}px;

  &:first-child {
    margin-left: 0;
  }
  &:last-child {
    margin-right: 0;
  }

  .nav-link {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid transparent;

    &,
    &:visited {
      text-decoration: none;
      color: #fff;
    }

    &.active {
      color: red;
    }
  }
`;

function MenuButton({ className, active, ...props }) {
  return (
    <button className={classnames(className, { active })} {...props}>
      <svg viewBox="0 0 40 40" width="40" height="40">
        <line
          x1="6"
          y1="10"
          x2="34"
          y2="10"
          stroke="#fff"
          strokeLinecap="round"
          strokeWidth="6"
          className="line-1"
        />
        <line
          x1="6"
          y1="20"
          x2="34"
          y2="20"
          stroke="#fff"
          strokeLinecap="round"
          strokeWidth="6"
          className="line-2"
        />
        <line
          x1="6"
          y1="20"
          x2="34"
          y2="20"
          stroke="#fff"
          strokeLinecap="round"
          strokeWidth="6"
          className="line-3"
        />
        <line
          x1="6"
          y1="30"
          x2="34"
          y2="30"
          stroke="#fff"
          strokeLinecap="round"
          strokeWidth="6"
          className="line-4"
        />
      </svg>
    </button>
  );
}

const StyledMenuButton = styled(MenuButton)`
  margin: 8px;
  padding: 0;
  border: none;
  background: none;
  outline: none;
  cursor: pointer;

  line {
    transition: all 0.2s linear;
    transform-origin: 50% 50%;
  }

  &.active {
    .line-1 {
      opacity: 0;
      transform: scale(0);
    }
    .line-2 {
      transform: rotate(45deg);
    }
    .line-3 {
      transform: rotate(-45deg);
    }
    .line-4 {
      opacity: 0;
      transform: scale(0);
    }
  }

  @media ${({ theme }) => theme.devices.small.query} {
    display: none;
  }
`;

const MenuNavList = styled(NavList)`
  .nav-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
`;

const MenuNavListItem = styled(NavListItem)`
  display: flex;

  & ~ & {
    border-top: 1px solid #444444;
  }

  .nav-link {
    flex: 1;
    line-height: 40px;
    padding: 8px 16px;

    &,
    &:visited {
      text-decoration: none;
      color: #000;
    }
  }
`;

class AppBar extends React.Component {
  state = {
    drawerActive: false,
  };

  componentDidMount() {
    this.historyUnlisten = this.props.history.listen(() => {
      this.setState({ drawerActive: false });
    });
  }

  componentWillUnmount() {
    this.historyUnlisten();
  }

  handleToggleMenu = () => {
    this.setState(state => ({ drawerActive: !state.drawerActive }));
  };

  navItems = [
    { children: "Home", to: "/", exact: true },
    { children: "Contact", to: "/contact", exact: true },
  ];

  render() {
    return (
      <>
        <AppBarGlobalStyles />
        <AppBarPlaceholder />
        <AppBarWrapper>
          <AppBarContent>
            <BarNavList>
              {this.navItems.map((navItem, idx) => (
                <BarNavListItem key={idx} {...navItem} />
              ))}
            </BarNavList>

            <StyledMenuButton
              onClick={this.handleToggleMenu}
              active={this.state.drawerActive}
            />
            <Drawer
              onOverlayClick={this.handleToggleMenu}
              active={this.state.drawerActive}
              style={{ paddingTop: "var(--app-bar-height)" }}
            >
              <h1>Hello</h1>

              <MenuNavList>
                {this.navItems.map((navItem, idx) => (
                  <MenuNavListItem key={idx} {...navItem} />
                ))}
              </MenuNavList>
            </Drawer>
          </AppBarContent>
        </AppBarWrapper>
      </>
    );
  }
}

export default withRouter(AppBar);
